import React from "react";
import { Zbutton } from "zerod";
// const {addClass, removeClass}=zTool;
// import { Row, Col, Button, Icon, message } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/Zbutton/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			render() {
				return (
					<span>
						<Zbutton type="default">default</Zbutton>
						<Zbutton type="primary">primary</Zbutton>
						<Zbutton type="success">success</Zbutton>
						<Zbutton type="danger">danger</Zbutton>
						<Zbutton type="danger" disabled>
							disabled
						</Zbutton>
						<Zbutton type="default" size="small">
							default small
						</Zbutton>
						<Zbutton type="primary" size="small">
							primary small
						</Zbutton>
						<Zbutton type="success" size="small">
							success small
						</Zbutton>
						<Zbutton type="danger" size="small">
							danger small
						</Zbutton>
					</span>
				);
			}
		}
		return <Myjavascript />;
	},
});
